function CommonIssueCard(props) {
    return (
        <th className="tableAlign">
            <div className="card w-100" style={{border: 'none', borderRadius: '22px', minHeight: '375px'}}>
                <img className="card-img" style={{borderRadius: '16px 16px 0 0'}} src={props.cardImg}></img>
                <div className="card-body bg-danger" style={{borderRadius: '0 0 16px 16px'}}>
                    <div className="card-title h4 font-weight-bold">{props.cardTitle}</div>
                    <div className="card-text" style={{fontSize: '14px'}}>{props.cardInfo}</div>
                </div>
            </div>
        </th>
    )
}

export default CommonIssueCard;