import { faCheck } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function CheckLiItem(props) {
    return (
        <li><FontAwesomeIcon icon={faCheck} style={{transform: 'translate(125%, 12.5%)'}} className="ml-1 position-absolute"/>{props.info}</li>
    )
}

export default CheckLiItem;