import {Carousel} from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";

import InfoItem from '../components/InfoItem';

import carousel1 from '../imgs/carousel1.jpg'
import carousel2 from '../imgs/carousel2.jpg'
import carousel3 from '../imgs/carousel3.jpg'
import carousel4 from '../imgs/carousel4.jpg'
import carousel5 from '../imgs/carousel5.jpg'
import carousel6 from '../imgs/carousel6.jpg'
import carousel7 from '../imgs/carousel7.jpg'

function Info() {
    return (
        <div className="text-center">
            <Carousel showThumbs={false} useKeyboardArrows={true} swipeable={true} infiniteLoop={true} showStatus={false} centerMode={true} centerSlidePercentage={34} selectedItem={3}>
                <img src={carousel1}/>
                <img src={carousel2}/>
                <img src={carousel3}/>
                <img src={carousel4}/>
                <img src={carousel5}/>
                <img src={carousel6}/>
                <img src={carousel7}/>
            </Carousel>
            <div class="accordion my-4" id="accordion-one" style={{width: '80%', transform: 'translateX(12.5%)'}}>
                <InfoItem accordionName="accordion-one" identifier="1" infoTitle="תקלות תוכנתיות" infoInfo={`תקלות אשר ניתן לתקן ולהציל, זאת באמצעות איתור הגורם הבעייתי ותיקונו,\nאו באמצעות פירמוט במקרה הגרוע.\nאת הקבצים והמידע תמיד ניתן לגבות במקרה של תקלה תוכנתית,\nוהרמת סיכון לרכיבי המחשב שואפת לאפס.`}/>
                <InfoItem accordionName="accordion-one" identifier="2" infoTitle="תקלות חומרתיות" infoInfo={`תקלות קשות יותר מתקלות תוכנתיות, תלוי בגורם וחומרת המצב,\nיכול להיות שאפשר ויכול להיות שאי אפשר להציל את הקבצים ואת המידע.\nהרבה תקלות חומרתיות אפשר לתקן ו/או להחליף, תלוי בחומרת המצב.\nמומלץ לא להתעכב ולשלוח לטיפול ברגע שעולה תקלה חומרתית.`}/>
                <InfoItem accordionName="accordion-one" identifier="3" infoTitle="תחזוק וניקוי" infoInfo={`תלוי בגורמים נוספים, כמו מיקום המחשב ותדירות השימוש שלו,\nוכמו כן לחות וטמפרטורת הסביבה שלו,\nגם את המחשב צריך לתחזק, לנקות ולהחליף משחה טרמית.\n\nהתחזוק הכי נפוץ הוא ניקוי אבק חיצוני ופנימי, מכיוון שאבק חוסם פעילות מאווררים תקינה,\nויכול להיכנס למקומות שיגרמו למחשב להפסיק להידלק,\nולתקלות אחרות שיפגינו תסמינים של תקלה חומרתית.\n\nחשוב לציין שניקוי מחשב לא מבצעים עם שואב אבק, ורק עם אוויר דחוס.`}/>
                <InfoItem accordionName="accordion-one" identifier="4" infoTitle="ניקוי עמוק" infoInfo={`תהליך שבו לוקחים את הרכיב, פותחים אותו ומפרקים אותו, ומבצעים ניקוי עדין לחלקים שבפנים.\n\nבמקרה של ניקוי לכרטיסי מסך, הוא כולל החלפת משחה טרמית,\nובכך שיפור ביצועים טרמים ושמירה על משך חיים ארוך.\n\nבמקרה של ספק כוח, במידה ונמצא מאוורר תקול או רועש, יש אופציה להחלפת המאוורר בתוספת.`}/>
                <InfoItem accordionName="accordion-one" identifier="5" infoTitle="גיבוי והעברת מידע" infoInfo={`שירות המוצע אצלנו, בו מגבים את המידע מכונן פנימי ומעבירים אותו אל אחסון חיצוני,\nומשם מעבירים אותו על פי דרישת הלקוח.\n\nתומכים ומגבים אחסון מסוג\nHDD, SSD, M.2 SATA, ,M.2 NVME, SD, MICRO SD`}/>
                <InfoItem accordionName="accordion-one" identifier="6" infoTitle="שדרוג כונן ל-SSD" infoInfo={`ברוב המחשבים, שיטת היצרן לחסוך בעלויות היא להכניס כונן מכני איטי למחשב, וכתוצאה מכך,\nהכונן הקשיח צורך יותר חשמל, פגיע ורגיש לזעזועים וטלטולים, עושה רעש מכני ומאט את כל פעילות המחשב.\nלמזלנו יש את כונן ה-SSD, שצורך פחות חשמל וללא חלקים מכנים, וכתוצאה מכך הוא שקט לחלוטין ולא רגיש לזעזועים כלל, ומהיר פי עד 10 מכונן קשיח!`}/>
            </div>
        </div>
    )
}

export default Info;