function ServiceInfoCard(props) {
    return (
        <div className={`d-flex mb-5 ${props.windowSize > 830 ? '' : 'row justify-content-center'}`} dir="rtl">
            <img className="mr-4 ml-1 serviceCardImg" src={props.infoImg}/>
            <div className={`text-white ${props.windowSize > 830 ? 'text-right' : 'text-center'}`}>
                <div className="serviceCardTitle">{props.infoTitle}</div>
                <div className="serviceCardInfo">{props.infoInfo}</div>
            </div>
        </div>
    )
}

export default ServiceInfoCard;