import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faEnvelope} from '@fortawesome/free-solid-svg-icons';

function Top() {
    return (
        <div>
            <a className="position-absolute badge text-white" href="mailto:contact@ecl.co.il" style={{right: '10%'}}>
                <FontAwesomeIcon icon={faEnvelope}/>
                <span className="ml-1">contact@ecl.co.il</span>
            </a>
        </div>
    )
}

export default Top;