import './css/Home.css';

import {Link} from 'react-router-dom';

import laptop from '../imgs/laptop.png';
import commonIssue1 from '../imgs/common-issue1.jpg';
import commonIssue2 from '../imgs/common-issue2.jpg';
import commonIssue3 from '../imgs/common-issue3.jpg';
import commonIssue4 from '../imgs/common-issue4.jpg';

import CheckLiItem from '../components/CheckLiItem';
import CommonIssueCard from '../components/CommonIssueCard';

function Home() {
    return (
        <div className="text-center purple-gradient color-block centerGradient" style={{backgroundColor: '#0e0e0e', color: 'black'}}>
            <div style={{width: '80%', transform: 'translateX(12.5%)'}}>
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <div dir="rtl" className="mainText">איך אוכל לעזור היום?</div>
                <ul dir="rtl" className="list-unstyled h5 mb-5 hoverWhite">
                    <CheckLiItem info="המחשב לא נדלק?"/>
                    <CheckLiItem info="המחשב מרעיש?"/>
                    <CheckLiItem info="המחשב איטי?"/>
                </ul>
                <p dir="rtl" className="h4 mx-5">כל תקלה חומרתית או תוכנתית ניתנת לתיקון אחרי <strong>אבחון אמיתי</strong>, בו אפשר לקבוע את התקלה ואת הפתרון!</p>
                <div className="container">
                    <button dir="rtl" className="btn btn-dark font-weight-bold blackBtn">לחצו כאן לקבלת אבחון בחינם!</button>
                </div>
                <img className="laptop" src={laptop}/>
                <div className="text-white" style={{fontSize: '3rem', textShadow: '1px 1px 3px black', paddingBottom: '60px'}}>תקלות נפוצות</div>
            </div>
            <table dir="rtl" className="table m-0">
                <tr>
                    <CommonIssueCard cardImg={commonIssue1} cardTitle="מסך כחול" cardInfo={`תקלה תוכנתית נפוצה במערכת ווינדוס, יכולה להגיע בתצורות שונות בהתאם למערכת הפעלה.\nלעיתים אף מצביעה על תקלה חומרתית.`}/>
                    <CommonIssueCard cardImg={commonIssue2} cardTitle="צפצוף מחשב" cardInfo={`תקלה חומרתית המתרחשת באתחול המחשב, ויכולה לנבוע ממגוון סיבות שחלקן מינוריות וחלקן קריטיות.`}/>
                    <CommonIssueCard cardImg={commonIssue3} cardTitle="מחשב רועש/חם/איטי" cardInfo={`תופעות שיכולות לנבוע מהמון סיבות,\nמתחזוק המחשב, עד תוכנות ווירוסים.\nבמרבית המקרים הטיפול הפותר הוא\nניקוי הרכיבים והחלפת משחה טרמית.`}/>
                    <CommonIssueCard cardImg={commonIssue4} cardTitle="שגיאות ותקלות נוספות" cardInfo={`תוכנות שפעלו פעם הפסיקו לעבוד?\nשגיאה מופיעה בפעולה שמתבצעת?\nאו שבכלל זו שגיאה רנדומלית?\nגם את השגיאות האלו אפשר לפתור.`}/>
                </tr>
            </table>
            <Link to="/info" dir="rtl" className="btn btn-primary p-3 px-5 my-3 mb-5" style={{fontSize: '1.1rem', fontWeight: 600}}>למידע נוסף לחצו כאן!</Link>
        </div>
    )
}

export default Home;