function Footer(props) {
    const styles =  {
        color: 'white',
        paddingTop: '3em',
        position: 'relative',
        bottom: 0,
        width: '100%',
        textAlign: 'center'
    }  
    return (
        <div style={styles}>
            <div className="container">
                <a href="https://saharzzz.github.io/saharhayon/" className={`${props.class} link-unstyled text-decoration-none`}>{`© ${new Date().getFullYear()} Copyright: Sahar Hayon`}</a>
            </div>
        </div>
    )
}

export default Footer;