import './css/Services.css';

import { Link } from 'react-router-dom';
import { useState } from "react";

import ServiceItem from '../components/ServiceItem';
import ServiceInfoCard from '../components/ServiceInfoCard';

import monitor from '../imgs/monitor.png';
import info1 from '../imgs/info1.png';
import info2 from '../imgs/info2.png';
import info3 from '../imgs/info3.png';
import info4 from '../imgs/info4.png';

function Services() {
    const [windowSize, setWindowSize] = useState(window.innerWidth);
    window.addEventListener('resize', () => setWindowSize(window.innerWidth));
    return (
        <div style={{backgroundColor: '#171d2b'}}>
            <div className="text-center" style={{width: '80%', transform: 'translateX(12.5%)'}}>
                <div className="h1 py-4 text-gray font-weight-bold">שירות האבחון הוא בחינם</div>
                <div>
                    <img src={monitor} style={{height: '20em'}}/>
                    <ul dir="rtl" className="list-unstyled text-right mt-3 p-0" style={{color: '#237fbc'}}>
                        <ServiceItem 
                        serviceTitle='התקנה/איסוף מבית הלקוח' 
                        servicePrice="20" 
                        serviceHome={true}
                        serviceInfo='שירות הגעת טכנאי מקצועי להתקנה/איסוף מחשב מבית הלקוח.'/>
                        <ServiceItem 
                        serviceTitle='ביקור טכנאי בבית הלקוח' 
                        servicePrice="250₪ - 450" 
                        serviceHome={true}
                        serviceInfo='שירות הגעת טכנאי מקצועי לטיפול ועזרה במגוון תקלות/התקנות בבית הלקוח.'/>
                        <ServiceItem 
                        serviceTitle='התקנת SSD דיסק מהיר (דיסק כלול במחיר)' 
                        servicePrice="100" 
                        serviceHome={false}
                        serviceInfo='כולל דיסק 240GB. אפשרות לשדרוג ל512GB בתוספת 100₪ או 1000GB בתוספת 200₪.'/>
                        <ServiceItem 
                        serviceTitle='התקנת תוכנות (גם אופיס)' 
                        servicePrice="50" 
                        serviceHome={false}
                        serviceInfo='שירות התקנת תוכנות והגדרתן במחשב.'/>
                        <ServiceItem 
                        serviceTitle='גיבוי והעברת מידע' 
                        servicePrice="50" 
                        serviceHome={false}
                        serviceInfo='שירות גיבוי מידע מכוננים פנימיים והעברת המידע.'/>
                        <ServiceItem 
                        serviceTitle='תחזוק וניקוי' 
                        servicePrice="70" 
                        serviceHome={false}
                        serviceInfo='שירות החלפת משחה טרמית. ניקוי אבק פנימי. *המחיר הוא לכל שירות בנפרד*'/>
                        <ServiceItem 
                        serviceTitle='תיקון תקלות/וירוסים' 
                        servicePrice="100" 
                        serviceHome={false}
                        serviceInfo='שירות תיקון תקלות חומרתיות ותוכניות. ניקוי וירוסים. *המחיר הוא התחלתי ויקבע לפי חומרת התקלה*'/>
                        <ServiceItem 
                        serviceTitle='הרכבה/פירוק' 
                        servicePrice="150" 
                        serviceHome={false}
                        serviceInfo='שירות הרכבה/פירוק למחשבים נייחים, כולל חיווט מקצועי.'/>
                        <ServiceItem 
                        serviceTitle='חילוץ מידע' 
                        servicePrice="150₪ - 350" 
                        serviceHome={false}
                        serviceInfo='חילוץ מידע מדיסק קשיח.'/>
                        <ServiceItem 
                        serviceTitle='ניקוי עמוק' 
                        servicePrice="200" 
                        serviceHome={false}
                        serviceInfo='שירות ניקוי עמוק ללוח הכרטיס מסך והחלפת משחה טרמית. ניקוי עמוק לפנים הספק כוח ולמאוורר. *המחיר הוא לכל שירות בנפרד*'/>
                        <ServiceItem 
                        serviceTitle='פרמוט והתקנת מערכת הפעלה' 
                        servicePrice="200" 
                        serviceHome={false}
                        serviceInfo='שירות פרמוט והתקנה נקייה, כולל מערכת הפעלה ווינדוס 10. ניתן להוסיף ניקוי אבק פנימי בתוספת 50₪.'/>
                    </ul>
                    <div className="mb-3 contactDiv">לשירות אחר או בקשות ספציפיות יש ליצור קשר</div>
                    <Link to="/contact"><button className="btn w-100 py-2 mb-4 contactBtn">צרו קשר</button></Link>
                    <div>
                        <ServiceInfoCard windowSize={windowSize} infoImg={info1} infoTitle="שימוש בכלים אנטי סטטים מותאמים" infoInfo={`במעבדת אלגנט יש התמדה בשימוש בערכת כלים אנטי סטטים,\nובכמות ביטים מגוונת על מנת להתאים לכל רכיב בנפרד מבלי לשחוק אותו.\n\nבטיחות וזהירות בעדיפות העליונה.`}/>
                        <ServiceInfoCard windowSize={windowSize} infoImg={info2} infoTitle="התקנות נכונות ומדויקות" infoInfo={`בהרבה מקרים חיווטים והרכבות הושמו בצורה שגויה,\nדבר אשר מהווה גורם מכריע לתקלות עתידיות,\nשחיקת המוצר לטווח הארוך ולביצועים נמוכים מן המצופה.\n\nבמעבדת אלגנט יש התייחסות לכל רכיב בנפרד,\nלפי הוראות היצרן במקרה של שינוי והרכבה נכונה.`}/>
                        <ServiceInfoCard windowSize={windowSize} infoImg={info3} infoTitle="ציוד לבדיקה והשוואה" infoInfo={`מעבדת אלגנט מצויידת בציוד רחב לבדיקות שונות,\nכך שיהיה אפשר לעשות את המקסימום על מנת לזהות תקלה\nבצורה מהירה ומדויקת ככל האפשר.`}/>
                        <div className={`d-flex mb-5 ${windowSize > 830 ? '' : 'row justify-content-center'}`} dir="rtl">
                            <img className="mr-4 ml-1 serviceCardImg" src={info4}/>
                            <div className={`text-white ${windowSize > 830 ? 'text-right' : 'text-center'}`}>
                                <div className="serviceCardTitle">צריכים ייעוץ? שלחו לנו הודעה!</div>
                                <div className="serviceCardInfo">{`צריכים ייעוץ לגבי שדרוג? קניית מחשב? או התלבטות כלשהיא בנושא?\nמוזמנים לשתף מה על הראש שלכם`} <Link to="/contact" className="text-decoration-none">בעמוד יצירת קשר.</Link>{`\n\nכמובן הייעוץ הוא בחינם, ובמידה ותצטרכו, גם האבחון.`}</div>
                            </div>
                        </div>
                        
                        <div className="p-1"></div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Services;