function InfoItem(props) {

    return (
        <div>
            <div>
                <div className="card-header border rounded-0" id={`heading-${props.identifier}`}>
                    <div id={`collapseHandler-${props.identifier}`} dir="rtl" className="text-right" style={{color: '#0071bc'}} type="button" data-toggle="collapse" data-target={`#collapse-${props.identifier}`} aria-expanded="true" aria-controls={`collapse-${props.identifier}`}>{props.infoTitle}</div>
                </div>
            </div>
            <div id={`collapse-${props.identifier}`} className="collapse border rounded-0" aria-labelledby={`heading-${props.identifier}`} data-parent={`#${props.accordionName}`}>
                <div dir="rtl" className="my-4 mr-4 mx-1 text-right" style={{color: '#29abe2', whiteSpace: 'pre-wrap', fontSize: '14px'}}>{props.infoInfo}</div>
            </div>
        </div>
    )
}

export default InfoItem;