import { Link, useLocation } from 'react-router-dom';

import FullLogo from '../imgs/full_logo.svg';

import './css/Navbar.css';

function Navbar() {
    const location = useLocation();
    return (
        <div style={{backgroundColor: '#0e0e0e'}}>
            <div className="container navbar-main-div">

            <Link to="/"><img src={FullLogo}/></Link>
            <nav class="navbar navbar-expand-lg navbar-dark" dir="rtl">
                <button class="navbar-toggler w-100" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarText">
                    <ul className="navbar-nav mr-auto mt-2 mt-lg-0 container">
                        <li class="nav-item">
                            <Link to="/" className={`badge ${location.pathname == '/' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>בית</Link>
                        </li>
                        <li class="nav-item">
                            <Link to="/services" className={`badge ${location.pathname == '/services' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>שירותים</Link>
                        </li>
                        <li class="nav-item">
                            <Link to="/about" className={`badge ${location.pathname == '/about' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>אודות</Link>
                        </li>
                        <li class="nav-item">
                            <Link to="/info" className={`badge ${location.pathname == '/info' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>מידע נוסף</Link>
                        </li>
                        <li class="nav-item">
                            <Link to="/contact" className={`badge ${location.pathname == '/contact' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>יצירת קשר</Link>
                        </li>
                    </ul>
                </div>
            </nav>
        </div>
        <br/>
        </div>
    )
}

export default Navbar;