import wrench from '../imgs/wrench.png';
import homeWrench from '../imgs/home-wrench.png';

function ServiceItem(props) {
    return (
        <div>
            <li className="d-flex justify-content-between font-weight-bold serviceItemTitle"><div><img src={props.serviceHome ? homeWrench : wrench}/>{props.serviceTitle}</div><div dir="ltr">{props.servicePrice}₪</div></li>
            <li className="serviceItemInfo">{props.serviceInfo}</li>
            <hr/>
        </div>
    )
}

export default ServiceItem;